import React from 'react'
import styled from 'styled-components'
import { Swiper,SwiperSlide } from 'swiper/react';
import { Navigation, Pagination} from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';


const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding:  0 24px;
  @media screen and (max-width: 400px) {
    padding:  0 6px;
    
  }
  .swiper-button-next,
  .swiper-button-prev {
    border-radius: 50%;
    background-color: #fff;
    color: #000;
    font-weight: bold;
    width: 44px;
    height: 44px;
  }
  .swiper-button-next::after,
  .swiper-button-prev::after {
    font-size: 10px;
  }
  .swiper-pagination {
    bottom: 10px !important;
  }
  .swiper-pagination-fraction, 
  .swiper-pagination-custom, 
  .swiper-pagination-progressbar {
    width: 80px;
    color: #fff;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 20px;
    padding: 5px 10px;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
  }
`;

const PaginationWrapper = styled.div`

  .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    background-color: black;
    opacity: 0.5;
    border-radius: 50%;
    margin: 0 5px;
  }
  .swiper-pagination-bullet-active {
    background-color: black;
  }
`;

const Top = styled.div`
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-top: 50px;
    width: 100%;

    padding: 10px;
    @media screen and (max-width: 400px) {
      padding: 5px;
      margin-top: 0;
      height: 10%;
    }
`
const H1 = styled.h1`
  
`
const Bottom = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;

    margin-bottom: 50px;


 
  @media screen and (max-width: 400px) {
      width: 100%;
      height: 90%;
    }
 
`
const Ul = styled.ul`
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;

    width: 100%;
`
const Li = styled.li`
list-style: none;
    width: 100%;
    height: 450px;
`
const Img = styled.img`
width: 100%;
  height: 450px;

`
const shoes = [
  {
    id : 1,
    img : "assets/airforce-1.png"
  },
  {
    id : 2,
    img : "assets/dunk.png"
  },
  {
    id : 3,
    img : "assets/airjordan-1.png"
  },
  {
    id : 4,
    img : "assets/airmax.png"
  },
  {
    id : 5,
    img : "assets/v2krun.png"
  },
  {
    id : 6,
    img : "assets/pegasus.png"
  },
  {
    id : 7,
    img : "assets/cortez.png"
  },
]
function SectionTwo() {
  return (
      <Wrapper>
      <Top>
      <H1>Always Iconic</H1>
      </Top>
      <Bottom>
      <Swiper
      modules={[Navigation, Pagination]}
      spaceBetween={10}
      style={{
        width : "100%"
      }}
      navigation
      pagination={{
        type: 'fraction'
      }}
      loop={true}
      breakpoints={{
        400: {
          slidesPerView: 1
        },
        600: {
          slidesPerView: 2
        },
        960: {
          slidesPerView: 3
        },
        1240: {
          slidesPerView: 4
        }
      }}
    >  
      <Ul >
        {shoes.map((item) => (
          <SwiperSlide key={item.id}>
          <Li><Img src={item.img} /></Li>
          </SwiperSlide>
        ))}
        </Ul>
    </Swiper>
    </Bottom>
    <PaginationWrapper />
    </Wrapper>
  )
}

export default SectionTwo
