import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    width: 100%;
    overflow: hidden;
    padding: 0 24px;
    @media screen and (max-width: 400px) {
      padding: 0 12px;
    }
`
const Top = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    width: 100%;

    padding: 10px;
    @media screen and (max-width: 400px) {
      padding: 5px;
      margin-top: 0;
      height: 10%;
    }
`
const H1 = styled.h1`
    font-size: 30px;
`
const Bottom = styled.div`
      width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    @media screen and (max-width: 960px) {
      overflow-x: scroll;
      &::-webkit-scrollbar {
    height: 8px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
    border-radius: 20px;
  }
  &::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 10px;
    border-radius: 20px;
  }
    }
 
`
const Ul = styled.ul`
width: 100%;
    display: flex;
    align-items: center;
    gap : 8px;
    
    width: 100%;
`
const Li = styled.li`
width: 100%;
      display: flex;
      flex-direction: column;

`
const A = styled.a`
    text-decoration: none;
    color: black;
`
const Img = styled.img`
width: 100%;
    object-fit: cover;
    height: 50%;
    @media screen and (max-width: 960px) {
        width: 400px;
      height: 100%;
    }
    @media screen and (max-width: 400px) {
        width: 200px;
      height: 100%;
    }
`
const P = styled.p`
    font-size: 20px;
    font-weight: bold;
    padding: 10px;
`
const category = [
    {
        id :1,
        img : "/assets/category-1.png",
        text : "의류"
    },
    {
        id :2,
        img : "/assets/category-2.png",
        text : "신발"
    },
    {
        id :3,
        img : "/assets/category-3.png",
        text : "장비 및 악세서리"
    }
]
function Category() {
  return (
    <Wrapper>
      <Top>
        <H1>카테고리별 구매하기</H1>
      </Top>
      <Bottom>
        <Ul>
        {category.map((item)=>(
            <Li key={item.id}>
                <A href='#'>
                    <Img src={item.img}></Img>
                    <P>{item.text}</P>
                </A>
            </Li>
        ))}
        </Ul>
      </Bottom>
    </Wrapper>
  )
}

export default Category
