import React, { useState } from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 100px;
  width: 100%;
  @media screen and (max-width: 960px) {
    margin-top: 50px;
  }
`;

const MainBox = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  width: 95%;
  @media screen and (max-width: 960px) {
    flex-direction: column;
  }
`;

const InnerBox = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: calc(100% / 4);
  gap: 12px;
  @media screen and (max-width: 960px) {
    width: 95%;
    align-items: center;
    border-bottom: 1px solid #e2e2e2;
    flex-direction: row;
    flex-wrap: wrap;
  }
`;

const MainBoxTitle = styled.h1`
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #111;
  @media screen and (max-width: 960px) {
    padding: 24px 24px 24px 0;
    cursor: pointer;
    width: 90%;
    justify-content: space-between;
  }
`;

const ToggleBtn = styled.button`
  width: 5%;
  height: 20px;
  color: black;
  background: transparent;
  display: none;
  border: none;
  cursor: pointer;
  &:hover {
    background-color: #e2e2e2;
  }
  @media screen and (max-width: 960px) {
    display: block;
  }
`;

const MainBoxTitleTwo = styled.h1`
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
  top: 0;
  font-size: 14px;
  color: #111;
  cursor: pointer;
  &:hover {
    color: #707072;
  }
  @media screen and (max-width: 960px) {
    position: relative;
    left: 0;
    padding: 24px 24px 24px 0;
    cursor: pointer;
  }
`;

const MainBoxText = styled.p`
  font-size: 14px;
  color: #707072;
  cursor: pointer;
  @media screen and (max-width: 960px) {
    width: 100%;
    padding-bottom: 10px;
    display: ${props => (props.show ? 'block' : 'none')};
  }
`;

const Img = styled.img`
  width: 20px;
  height: 20px;
`;

const CopyBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 95%;
  margin-top: 20px;
  padding-bottom: 80px;
  border-bottom: 1px solid #e2e2e2;
  
`;

const Ul = styled.ul`
  display: flex;
  list-style: none;
  gap: 20px;
  @media screen and (max-width: 960px) {
    flex-direction: column;
  }
`;

const Li = styled.li`
  color: #707072;
`;

const A = styled.a`
  font-size: 14px;
  text-decoration: none;
  color: #707072;
`;

const menu = [
  {
    id: 1,
    title: "안내",
    items: ["멤버가입", "매장찾기", "나이키 저널"],
  },
  {
    id: 2,
    title: "고객센터",
    items: ["주문배송조회", "반품 정책", "결제 방법", "공지사항", "문의하기"],
  },
  {
    id: 3,
    title: "회사소개",
    items: ["About Nike", "소식", "채용", "투자자", "지속가능성"],
  },
];

function BottomFooter() {
  const [activeIndex, setActiveIndex] = useState(null);

  const handleToggle = index => {
    setActiveIndex(activeIndex === index ? null : index);
  };

  return (
    <Wrapper>
      <MainBox>
        {menu.map((items, index) => (
          <InnerBox key={items.id}>
            <MainBoxTitle>{items.title}</MainBoxTitle>
            <ToggleBtn onClick={() => handleToggle(index)}>
              <Img src={activeIndex === index ? '/assets/up.svg' : '/assets/down.svg'} />
            </ToggleBtn>
            {items.items.map((subitems, subindex) => (
               <MainBoxText key={subindex} show={activeIndex === index ? 'true' : undefined}>
                {subitems}
              </MainBoxText>
            ))}
          </InnerBox>
        ))}
        <InnerBox>
          <MainBoxTitleTwo>
            <Img src="/assets/globe.png" />
            대한민국
          </MainBoxTitleTwo>
        </InnerBox>
      </MainBox>
      <CopyBox>
        <Ul>
          <Li>© 2024 Nike, Inc. All Rights Reserved</Li>
          <Li>
            <A href="#">이용약관</A>
          </Li>
          <Li>
            <A href="#">개인정보처리방침</A>
          </Li>
          <Li>
            <A href="#">위치 기반 서비스 약관</A>
          </Li>
          <Li>
            <A href="#">영상정보처리기기 운영 방침</A>
          </Li>
        </Ul>
      </CopyBox>
    </Wrapper>
  );
}

export default BottomFooter;
