import React from 'react';
import styled from 'styled-components';

const Bg = styled.div`
    position: absolute;
    top: 100%;
    left: -650px;
    background-color: rgba(0,0,0,0.3);
    height: 970vh;
    width: 200vw;
`
const Wrapper = styled.div`
  position: absolute;
  width: 200vw;
  background-color: white;
  z-index: 1000;
  padding: 20px 0;
`;

const Container = styled.div`
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 1200px;
  margin-left: 310px;
  padding: 0 20px;
`;

const Ul = styled.ul`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  margin: 0;
  padding: 0;
`;

const Li = styled.li`
  list-style: none;
  margin: 0;
  padding: 0;
`;

const A = styled.a`
  padding: 5px 10px;
  color: black;
  text-decoration: none;
  font-weight: bold;
  display: block;
`;

const P = styled.p`
  cursor: pointer;
  font-size: 14px;
  padding: 5px 10px;
  margin: 0;

  &:hover {
    font-weight: bold;
  }
`;

const sublist = [
  {
    id: 1,
    title: 'New & Featured',
    item: ['신제품 전체', '베스트셀러']
  },
  {
    id: 2,
    title: 'Collections',
    item: ['나이키 페가수스 41', '여름 클래식 룩']
  },
  {
    id: 3,
    title: '가이드',
    item: ['러닝화 가이드', '축구화 가이드', '농구화 가이드']
  }
];

const sublist2 = [
  {
    id: 4,
    title: 'Shop Icons',
    item: ['에어포스 1', '줌 보메로 5', '코르테즈', 'V2K', '덩크', '페가수스', '인피니티 런', '메트콘']
  },
  {
    id: 5,
    title: 'SNKRS'
  }
];

const sublist3 = [
  {
    id: 6,
    title: 'Mens 신제품',
    item: ['신발', '의류']
  },
  {
    id: 7,
    title: 'Womens 신제품',
    item: ['신발', '의류']
  },
  {
    id: 8,
    title: 'Kids 신제품',
    item: ['신발', '의류']
  }
];

const sublist4 = [
  {
    id: 9,
    title: '조던',
    item: ['Men', 'Women', 'Kids']
  },
  {
    id: 10,
    title: 'Shop by Sport',
    item: ['러닝', '트레이닝 & 짐', '축구', '테니스', '수영', '요가', '골프', '농구']
  }
];

const renderSubList = (list) => (
  <Ul key={list.id}>
    {list.map((sub) => (
      <Li key={sub.id}>
        <A href="#">{sub.title}</A>
        {sub.item && sub.item.map((item, index) => <Ul  key={index}><Li><P>{item}</P></Li></Ul>)}
      </Li>
    ))}
  </Ul>
);

function Submenu() {
  return (
    <Bg>
    <Wrapper>
      <Container>
        {renderSubList(sublist)}
        {renderSubList(sublist2)}
        {renderSubList(sublist3)}
        {renderSubList(sublist4)}
      </Container>
    </Wrapper>
    </Bg>
  );
}

export default Submenu;
