import React, { useState } from 'react';
import styled from 'styled-components';
import Submenu from './Submenu';
import Submenu2 from './Submenu2';
import Submenu3 from './Submenu3';
import Submenu4 from './Submenu4';
import Submenu5 from './Submenu5';

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  @media screen and (max-width: 960px) {
    display: none;
  }
`;

const Ul = styled.ul`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
`;

const Li = styled.li`
  list-style: none;
  position: relative;
`;

const A = styled.a`
  display: block;
  padding: 10px;
  color: black;
  text-decoration: none;
  font-weight: bold;

  &:hover {
    border-bottom: 2px solid black;
  }
`;

const menulist = [
  {
    id: 1,
    text: 'New & Featured'
  },
  {
    id: 2,
    text: 'Men'
  },
  {
    id: 3,
    text: 'Women'
  },
  {
    id: 4,
    text: 'Kids'
  },
  {
    id: 5,
    text: 'Sale'
  }
];

function Navigation() {
  const [activeSubmenu, setActiveSubmenu] = useState(null);

  const handleMouseEnter = (id) => {
    setActiveSubmenu(id);
  };

  const handleMouseLeave = () => {
    setActiveSubmenu(null);
  };

  return (
    <Wrapper onMouseLeave={handleMouseLeave}>
      <Ul>
        {menulist.map((list) => (
          <Li
            key={list.id}
            onMouseEnter={() => handleMouseEnter(list.id)}
          >
            <A href="#">{list.text}</A>
            {list.id === 1 && activeSubmenu === 1 && <Submenu />}
            {list.id === 2 && activeSubmenu === 2 && <Submenu2 />}
            {list.id === 3 && activeSubmenu === 3 && <Submenu3 />}
            {list.id === 4 && activeSubmenu === 4 && <Submenu4 />}
            {list.id === 5 && activeSubmenu === 5 && <Submenu5 />}
          </Li>
        ))}
      </Ul>
    </Wrapper>
  );
}

export default Navigation;
