import React from 'react'
import styled from 'styled-components'
import { useState,useEffect } from 'react'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 24px;

    width: 100%;
    @media screen and (max-width: 400px) {
      padding: 0 12px;
    }
    @media screen and (max-width:400px) {
           padding: 0 6px;
    }
`
const Container = styled.div`
    width: 100%;
    padding: 0 6px;
    @media screen and (max-width:400px) {
            padding: 0;
    }
`
const ImgBox = styled.div`
    width: 100%;
    height: 100vh;
    @media screen and (max-width:960px) {
        height: 70vh;
    }
`
const Img = styled.img`
     object-position: 50% 50%;
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        @media screen and (max-width:960px) {
            object-position: 5%;
    }
    @media screen and (max-width:400px) {
            object-position: 15%;
    }
`
const H1 = styled.h1`
    text-align: center;
    font-size: 56px;
    font-weight: bold;
    padding: 10px;
    margin: 10px 0;

    @media screen and (max-width:960px) {
        font-size: 36px;
    }
`
const P = styled.p`
    margin-top: 10px;
    text-align: center;
    font-weight: bold;
    @media screen and (max-width:960px) {
    }
`
const ButtonBox = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;

    padding: 10px;
    margin: 10px 0;

`
const Button = styled.button`
    padding: 10px;
    background-color: black;
    color: white;
    border-radius : 24px;
    font-weight: bold;
    cursor: pointer;
`
const ButtonTwo = styled.button`
    padding: 10px;
    background-color: black;
    color: white;
    border-radius : 24px;
    font-weight: bold;
    cursor: pointer;
`
const jdi = [
    {
        id : 1,
        img : "/assets/nike-just-do-it.jpg",
        title : "스타일로 완성 하는 여름",
        text : "나이키 썸머 스타일과 함께 나만의 스타일로 여름을 즐겨보세요.",
        btntext : "자세히 보기",
        btntext2 : "구매하기"
    },
    {
        id : 2,
        img : "/assets/nike-just-do-it1.jpg",
        title : "대한민국 축구 국가대표팀 컬렉션",
        text : '한국의 전통적인 건축물의 아름다움과 강인함에서 영감을 받아 탄생한 유니폼',
        text2 : '그리고 그라운드 밖에서도 즐길 수 있는 다양한 제품들을 지금 만나 보세요.',
        btntext : "자세히 보기",
        btntext2 : "구매하기"
    },
    {
        id : 3,
        name : "매드 브릴리언스 팩",
        title : "놀랍도록 탁월한 플레이",
        img : "/assets/nike-just-do-it2-1.jpg",
        imgAlt : "/assets/nike-just-do-it2.jpg",
        text : "가장 중요한 순간에 탁월한 기량을 발휘해 보세요.",
        btntext : "자세히 보기",
        btntext2 : "구매하기"
    },
    {
        id : 4,
        name : "출시 예정",
        img : "/assets/nike-just-do-it3.jpg",
        title : "나이키 페가수스 41",
        text : "새로워진 리액트X 폼과 에어 줌으로 한 차원 높은 반응성의 에너지를 경험해 보세요.",
        btntext : "남성용 알림 받기",
        btntext2 : "여성용 알림 받기"
    },
]



function JustDoIt() {
    const [isMobile, setIsMobile] = useState(window.innerWidth <= 960);

    useEffect(() => {
        const handleResize = () => {
            setIsMobile(window.innerWidth <= 960);
        };
        
        window.addEventListener('resize', handleResize);
        
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);
  return (
    <Wrapper>
      <Container>
        {jdi.map((item)=>(
            <div key={item.id}>
            <ImgBox>
            <Img src={isMobile && item.id === 3 ? item.imgAlt : item.img} alt="" />
            </ImgBox>
            <P>{item.name}</P>
            <H1>{item.title}</H1>
            <P>{item.text}</P>
            <P>{item.text2}</P>
            <ButtonBox>
                    <Button>{item.btntext}</Button><ButtonTwo>{item.btntext2}</ButtonTwo>
            </ButtonBox>
            </div>
        ))}
      </Container>
    </Wrapper>
  )
}

export default JustDoIt
