import React, { useRef, useState } from 'react';
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100%;

    padding: 0 24px;
    @media screen and (max-width: 400px) {
      padding: 0 6px;
    }
`
const Top = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    width: 100%;

    padding: 10px;
    @media screen and (max-width: 400px) {
      padding: 5px;
      margin-top: 0;
      height: 10%;
    }
`
const H1 = styled.h1`

`
const BtnBox = styled.div`
      display: flex;
      align-items: center;
      justify-content: center;

      gap: 8px;
`
const Btn = styled.button`
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    border-radius: 50%;
    font-size: 16px;
    font-weight: bold;
    border: none;

    &:hover {
        background-color: #e2e2e2;
    }

    @media screen and (max-width: 960px) {
        display: none;
    }
`
const LeftBtn = styled(Btn)`
`
const RightBtn = styled(Btn)`
`
const Bottom = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;

    margin-bottom: 50px;


 
  @media screen and (max-width: 400px) {
      width: 100%;
      height: 50%;
    }
 
`
const Ul = styled.ul`
    display: flex;
    align-items: center;
    list-style: none;
    gap : 8px;
    overflow-x: scroll;
    width: 100%;
    margin-bottom: 20px;
    &::-webkit-scrollbar {
    height: 8px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
    border-radius: 20px;
  }
  &::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 10px;
    border-radius: 20px;
  }
    @media screen and (max-width: 400px) {
      height: 100%;
      margin-bottom: 0;
    }

`
const Li = styled.li`
      display: flex;
      flex-direction: column;
@media screen and (max-width: 400px) {
      width: 100%;
      height: 100%;
    }
`
const Img = styled.img`
    object-fit: contain;
    @media screen and (max-width: 400px) {
      height: 100%;
      object-fit: cover;
    }
`
const P = styled.p`
    font-size: 28px;
    font-weight: bold;
    padding: 10px;
`
const slider = [
  {
    id : 1,
    img : "/assets/section-1.jpg",
    text : "여성샌들&슬라이드"
  },
  {
    id : 2,
    img : "/assets/section-2.jpg",
    text : "에어맥스 90"
    
  },
  {
    id : 3,
    img : "/assets/section-3.jpg",
    text : "프리 메트콘 9"
  },
  {
    id : 4,
    img : "/assets/section-4.jpg",
    text : "나이키 덩크"
  },
  {
    id : 5,
    img : "/assets/section-5.jpg",
    text : "젠비"
  },
  {
    id : 6,
    img : "/assets/section-6.jpg",
    text : "에어 조던 1"
  },
  {
    id : 7,
    img : "/assets/section-7.jpg",
    text : "나이키 V2K 런"
  }
]
function Section() {
  const scrollRef = useRef(null);
  const [index, setIndex] = useState(0);
  const maxIndex = slider.length - 3;

  const scrollLeft = () => {
      setIndex(prevIndex => (prevIndex > 0 ? prevIndex - 1 : 0));
      if (scrollRef.current) {
          scrollRef.current.scrollLeft -= scrollRef.current.offsetWidth / 3;
      }
  };

  const scrollRight = () => {
      setIndex(prevIndex => (prevIndex < maxIndex ? prevIndex + 1 : maxIndex));
      if (scrollRef.current) {
          scrollRef.current.scrollLeft += scrollRef.current.offsetWidth / 3;
      }
  };
  return (
    <Wrapper>
      <Top>
        <H1>
          Trending Now
        </H1>
        <BtnBox>
          <LeftBtn onClick={scrollLeft}>&lt;</LeftBtn>
          <RightBtn onClick={scrollRight}>&gt;</RightBtn>
        </BtnBox>
      </Top>
      <Bottom>
        <Ul style={{ transform: `translateX(-${index - 2}%)` }} ref={scrollRef}>
        {slider.map((item)=>(
          <Li key={item.id}>
            <Img src={item.img}  alt={item.text} />
            <P>{item.text}</P>
          </Li>
        ))}
        </Ul>
      </Bottom>
    </Wrapper>
  )
}

export default Section
