import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    width: 100%;
    overflow: hidden;
    padding: 0 24px;
    @media screen and (max-width: 400px) {
      padding: 0 12px;
    }
`
const Top = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
    width: 100%;

    padding: 10px;
    @media screen and (max-width: 400px) {
      padding: 5px;
      margin-top: 0;
      height: 10%;
    }
`
const H1 = styled.h1`
    font-size: 30px;
`
const Bottom = styled.div`
      width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    @media screen and (max-width: 960px) {
      overflow-x: scroll;
      &::-webkit-scrollbar {
    height: 8px;
  }
  &::-webkit-scrollbar-track {
    background: #fff;
    border-radius: 20px;
  }
  &::-webkit-scrollbar-thumb {
    background: #555;
    border-radius: 10px;
    border-radius: 20px;
  }
    }
 
`
const Ul = styled.ul`
width: 100%;
    display: flex;
    align-items: center;
    gap : 8px;
    
    width: 100%;
`
const Li = styled.li`
position: relative;
width: 100%;
      display: flex;
      flex-direction: column;

`
const A = styled.a`
    text-decoration: none;
    color: black;
`

const P = styled.p`
position: absolute;
bottom: 150px;
    left: 50px;
    font-weight: bold;
    color: white;
    font-size: 16px;
    @media screen and (max-width:1240px) {
        left: 30px;
    }
    @media screen and (max-width:960px) {
        font-size: 14px;
    }
`
const H3 = styled.h3`
    position: absolute;
    bottom: 100px;
    left: 50px;
    color: white;
    @media screen and (max-width:1240px) {
        left: 30px;
    }
    @media screen and (max-width:960px) {
        font-size: 14px;
    }
`
const Img = styled.img`
z-index: -1;
width: 100%;
    object-fit: cover;
    height: 50%;
    @media screen and (max-width: 960px) {
        width: 400px;
      height: 100%;
    }
    @media screen and (max-width: 400px) {
        width: 300px;
      height: 100%;
    }
`
const Button = styled.button`
    width: 110px;
    color: black;
    background-color: white;
    padding: 10px;
    border-radius: 20px;
    border: none;
    font-weight: bold;
    font-size: 16px;
    position: absolute;
    bottom: 30px;
    left: 50px;
    cursor: pointer;
    &:hover{
        background-color: #e2e2e2;
    }
    @media screen and (max-width:1240px) {
        left: 30px;
    }
    @media screen and (max-width: 400px) {
        width: 80px;
        font-size: 10px;
    }
`
const togetherrun = [
    {
        id : 1,
        title : "매장 찾기",
        text : "매장에서 완벽한 핏 찾기",
        btntext : "매장 찾기",
        img : "/assets/nike-run-1.png"
    },
    {
        id : 2,
        title : "나이키 런 클럽(NRC)",
        text : "터치 한 번으로 만나는 커뮤니티와 코치",
        btntext : "자세히 보기",
        img : "/assets/nike-run-2.png"
    },
    {
        id : 3,
        title : "러닝화 찾기",
        text : "내게 딱 맞는 러닝화 찾기",
        btntext : "자세히 보기",
        img : "/assets/nike-run-3.png"
    },
]
function TogetherRun() {
  return (
    <Wrapper>
      <Top>
        <H1>함께 달려보세요</H1>
      </Top>
      <Bottom>
      <Ul>
        {togetherrun.map((item)=>(
            <Li key={item.id}>
                <A href='#'>
                    <Img src={item.img}></Img>
                    <P>{item.title}</P>
                    <H3>{item.text}</H3>
                    <Button>{item.btntext}</Button>
                </A>
            </Li>
        ))}
        </Ul>
      </Bottom>
    </Wrapper>
  )
}

export default TogetherRun
