import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 36px;
    background-color: #F5F5F5;
    padding : 0 24px;
    @media screen and (max-width:960px) {
        display: none;
    }
`
const Logo = styled.a`
   padding: 20px 10px 10px 20px;
`
const Img = styled.img`
    width: 30px;
    height: 30px;
    scale: 1.5;
    object-fit: cover;
    margin-left: 20px;
`
const Nav = styled.ul`
    display: flex;
    align-items: center;
    justify-content: center;

    list-style: none;
`
const NavList = styled.li`
    display: flex;
    align-items: center;
    justify-content: center;
`
const A = styled.a`
    text-decoration: none;
`
const P = styled.p`
    color: black;
    font-size: 10px;
    font-weight: bold;
`
const VerticalLine = styled.div`
    font-size: 10px;
    margin: 0 10px;
`
const navlist = [
    {
        id : 1,
        text : "매장 찾기",
        bar : "|"
    },
    {
        id : 2,
        text : "고객센터",
        bar : "|"
    },
    {
        id : 3,
        text : "가입하기",
        bar : "|"
    },
    {
        id : 4,
        text : "로그인",
    },
]
function Top() {
  return (
    <Wrapper>
        <Nav>
            <Logo>
                <Img src='/assets/air_jordan_logo.png'/>.
                <Img src='/assets/convers.png'/>
            </Logo>
        </Nav>
        <Nav>
            {navlist.map((list)=>(
                <NavList key={list.id}>
                <A href='#'><P>{list.text}</P></A>
                <VerticalLine>{list.bar}</VerticalLine>
            </NavList>
            ))}
        </Nav>
    </Wrapper>
  )
}

export default Top
