import React from 'react';
import styled from 'styled-components';

const Bg = styled.div`
    position: absolute;
    top: 100%;
    left: -1100px;
    background-color: rgba(0,0,0,0.3);
    height: 970vh;
    width: 200vw;
`
const Wrapper = styled.div`
  position: absolute;
  width: 200vw;
  background-color: white;
  z-index: 1000;
  padding: 20px 0;
`;

const Container = styled.div`
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 1200px;
  margin-left: 350px;
  padding: 0 20px;
`;

const Ul = styled.ul`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  margin: 0;
  padding: 0;
`;

const Li = styled.li`
  list-style: none;
  margin: 0;
  padding: 0;
`;

const A = styled.a`
  padding: 5px 10px;
  color: black;
  text-decoration: none;
  font-weight: bold;
  display: block;
`;

const P = styled.p`
  cursor: pointer;
  font-size: 14px;
  padding: 5px 10px;
  margin: 0;

  &:hover {
    font-weight: bold;
  }
`;

const sublist = [
  {
    id: 1,
    title: 'Sale',
    item: ['All Sales', 'Sale 신발','Sale 의류', 'Sale 용품']
  }
];

const sublist2 = [
  {
    id: 2,
    title: 'Mens Sale',
    item: ['신발','의류','용품']
  }
];

const sublist3 = [
  {
    id: 3,
    title: 'Womens Sale',
    item: ['신발','의류','용품']
  }
];

const sublist4 = [
  {
    id: 4,
    title: 'Kids Sale',
    item: ['신발','의류','용품']
  }
];
const sublist5 = [
  {
    id: 5,
    title: '할인율',
    item: ['10% 미만','10%~20% 미만','30% 이상']
  }
];

const renderSubList = (list) => (
  <Ul key={list.id}>
    {list.map((sub) => (
      <Li key={sub.id}>
        <A href="#">{sub.title}</A>
        {sub.item && sub.item.map((item, index) => <Ul  key={index}><Li><P>{item}</P></Li></Ul>)}
      </Li>
    ))}
  </Ul>
);

function Submenu2() {
  return (
    <Bg>
    <Wrapper>
      <Container>
        {renderSubList(sublist)}
        {renderSubList(sublist2)}
        {renderSubList(sublist3)}
        {renderSubList(sublist4)}
        {renderSubList(sublist5)}
      </Container>
    </Wrapper>
    </Bg>
  );
}

export default Submenu2;
