import React, { useState, useEffect } from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 100px;
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 95%;
  border-bottom: 2px solid #e2e2e2;
  padding: 30px;
  @media screen and (max-width: 1240px) {
    width: 100%;
  }
  @media screen and (max-width: 600px) {
    width: 95%;
    padding: 10px 0 70px 0;
  }
`;

const Ul = styled.ul`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 60%;
  overflow: hidden;
  padding: 0;
  margin: 0;
  list-style: none;
  @media screen and (max-width: 1240px) {
    width: 100%;
  }
  @media screen and (max-width: 600px) {
    flex-direction: column;
    cursor: pointer;
    gap: 12px;
  }
`;

const Li = styled.li`
  margin: 0 10px;
  flex: 1;
  position: relative;
`;

const H4 = styled.h4`
  font-size: 20px;
  margin-bottom: 10px;
  @media screen and (max-width: 600px) {
    cursor: pointer;
  }
`;

const SubList = styled.ul`
  list-style: none;
  height: ${({ expanded }) => (expanded ? 'auto' : '170px')};
  padding: 0;
  margin: 0;
  overflow: hidden;
  transition: height 0.3s ease;
  @media screen and (max-width: 600px) {
    display: ${({ expanded }) => (expanded ? 'block' : 'none')};
  }
`;

const SubListItem = styled.li`
  margin: 20px 0;
  &:hover {
    font-weight: bold;
  }
`;

const A = styled.a`
  text-decoration: none;
  color: #707072;
  display: block;
  margin: 5px 0;
  font-size: 16px;
`;

const list = [
  {
    id: 1,
    title: 'Featured',
    items: ['에어 포스 1', '조던 1', '에어맥스 97', '에어맥스 90', '블레이저', '리액트', '페가수스']
  },
  {
    id: 2,
    title: '신발',
    items: ['전체 보기', '조던 신발', '러닝 신발', '농구 신발', '테니스 신발', '트레이닝 신발', 'Sale 신발']
  },
  {
    id: 3,
    title: '의류',
    items: ['전체 보기', '탑 & 티셔츠', '쇼츠', '후디 & 풀오버', '조거 & 스웻팬츠', '스포츠 브라', '팬츠 & 타이츠', '양말', '요가', 'NikeLab', 'Sale 의류']
  },
  {
    id: 4,
    title: 'Kids',
    items: ['베이비 신발', '키즈 신발', '키즈 농구 신발', '키즈 러닝 신발', '키즈 조던 신발', '키즈 의류', '키즈 백팩', '키즈 양말', 'Sale 키즈']
  }
];

function TopFooter() {
  const [expandedIndex, setExpandedIndex] = useState(null);
  const [hovered, setHovered] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 600);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const handleMouseEnter = () => {
    if (!isMobile) {
      setHovered(true);
    }
  };

  const handleMouseLeave = () => {
    if (!isMobile) {
      setHovered(false);
    }
  };

  const handleClick = (index) => {
    if (isMobile) {
      setExpandedIndex(expandedIndex === index ? null : index);
    }
  };

  return (
    <Wrapper>
      <Container>
        <Ul onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
          {list.map((item, index) => (
            <Li key={item.id}>
              <H4 onClick={() => handleClick(index)}>{item.title}</H4>
              <SubList expanded={hovered || expandedIndex === index ? true : undefined} >
                {item.items.map((subItem, subIndex) => (
                  <SubListItem key={subIndex}>
                    <A href="#">{subItem}</A>
                  </SubListItem>
                ))}
              </SubList>
            </Li>
          ))}
        </Ul>
      </Container>
    </Wrapper>
  );
}

export default TopFooter;
