import React from 'react'
import styled from 'styled-components'
import Navigation from './components/Navigation'
import Top from './components/Top'
import Promotion from './components/Promotion'

const Wrapper = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    height: 70px;
    background-color: white;

    padding : 0 24px;
`

const Logo = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 45px;
    @media screen and (max-width:960px) {
        margin: 0;
    }
`
const LogoImg = styled.img`
    width: 70px;
    height: 70px;

`
const SearchBar = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;

    gap: 10px;

   cursor: pointer;
`
const SearchBox = styled.div`
    position: relative;
    left: -10px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    
    &:hover{
        background-color: #fff;
        border: 1px solid #e2e2e2;
    }
`
const SearchImg = styled.img`
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 25px;
    border-radius: 50%;
`
const InputBox = styled.div`
    width: 215px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #e2e2e2;
    border-radius: 24px;

    padding: 5px;
    
    @media screen and (max-width:960px) {
        background-color: #fff;
    }
    
`
const Input = styled.input`
    width: 150px;
    padding: 5px;
    border: none;
    background-color: #e2e2e2;
    

    &:active{
        border: none;
    }
   
    @media screen and (max-width:960px) {
        display: none;
    }
`
const IconImg = styled.img`
    width: 20px;
    margin-right: 10px;


    &:last-child {
        display: none;

        @media screen and (max-width:960px) {
        display: block;
    }
    }
`
function Header() {
  return (
    <div>
    <Top />
    <Wrapper>
        <Logo>
            <LogoImg src='/assets/nike-logo.png'></LogoImg>
        </Logo>
        <Navigation />
        <SearchBar>
            <InputBox>
                <SearchBox><SearchImg src='/assets/search.svg' ></SearchImg></SearchBox>
                <Input type='text' placeholder='검색'></Input>
            </InputBox>
            <IconImg src='/assets/heart.svg'></IconImg>
            <IconImg src='/assets/bag.svg'></IconImg>
            <IconImg src='/assets/bars.svg'></IconImg>
        </SearchBar>
       
    </Wrapper>
    <Promotion />
    </div>
  )
}

export default Header
