import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    gap: 8px;
    margin: 5px 0;

    background-color: #F5F5F5;
`
const P = styled.p`
    font-weight: 500;
`
const A = styled.a`
    font-size: 12px;
    font-weight: bold;
    text-decoration: underline;
`

function Promotion() {
  return (
    <Wrapper>
      <P>개인정보 처리방침 개정 안내</P>
      <A>자세히 보기</A>
    </Wrapper>
  )
}

export default Promotion
