import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  margin-top: 20px;
  width: 100%;

`;

const Container = styled.div`
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  width: 90%;
  padding-bottom: 50px;
  gap: 12px;
  @media screen and (max-width:960px) {
        flex-direction: column;
        
  }
`;

const LeftBox = styled.div`
display: flex;
flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  width: 50%;
  @media screen and (max-width:960px) {
        width: 100%;
  }
`;

const RightBox = styled.div`
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  width: 50%;
  @media screen and (max-width:960px) {
        width: 100%;
  }
`;

const P = styled.p`
  color: #707072;
  font-size: 14px;
  
`;

const Span = styled.span``;

const A = styled.a`
  color: #707072;
  margin-left: 5px;
  &:hover{
    color: black;
  }
`;

function Information() {
  return (
    <Wrapper>
      <Container>
        <LeftBox>
          <P>
            <Span>
              (유)나이키코리아 대표 Kimberlee Lynn Chang Mendes, 킴벌리 린 창 멘데스 | 서울 강남구 테헤란로 152 강남파이낸스센터 30층 | 통신판매업신고번호 2011-서울강남-03461 | 등록번호
            </Span>
            <A href='#'>사업자 정보 확인</A>
          </P>
          <P>
            <Span>
              고객센터 전화 문의 <A href='#'>080-022-0182</A> FAX 02-6744-5880 | 이메일 <A href='#'>service@nike.co.kr</A> | 호스팅서비스사업자 (유)나이키코리아
            </Span>
          </P>
        </LeftBox>
        <RightBox>
          <P>
            <Span>
              현금 등으로 결제 시 안전 거래를 위해 나이키 쇼핑몰에서 가입하신 한국결제네트웍스 유한회사의 구매안전 서비스<A href='#'>&#40;결제대금예치&#41;</A>를 이용하실 수 있습니다.
            </Span>
            <Span>결제대금예치 콘텐츠산업진흥법에 의한 콘텐츠 보호 안내 <A href='#'>자세히 보기</A></Span>
          </P>
        </RightBox>
      </Container>
    </Wrapper>
  );
}

export default Information;
