import React from 'react';
import styled from 'styled-components';

const Bg = styled.div`
    position: absolute;
    top: 100%;
    left: -1000px;
    background-color: rgba(0,0,0,0.3);
    height: 970vh;
    width: 200vw;
`
const Wrapper = styled.div`
  position: absolute;
  width: 200vw;
  background-color: white;
  z-index: 1000;
  padding: 20px 0;
`;

const Container = styled.div`
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 1200px;
  margin-left: 315px;
  padding: 0 20px;
`;

const Ul = styled.ul`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  margin: 0;
  padding: 0;
`;

const Li = styled.li`
  list-style: none;
  margin: 0;
  padding: 0;
`;

const A = styled.a`
  padding: 5px 10px;
  color: black;
  text-decoration: none;
  font-weight: bold;
  display: block;
`;

const P = styled.p`
  cursor: pointer;
  font-size: 14px;
  padding: 5px 10px;
  margin: 0;

  &:hover {
    font-weight: bold;
  }
`;

const sublist = [
  {
    id: 1,
    title: 'New & Featured',
    item: ['신제품 전체', '베스트셀러']
  },
  {
    id: 2,
    title: 'Collections',
    item: ['에어 리프트', '반팔 티셔츠']
  },
];

const sublist2 = [
  {
    id: 3,
    title: '신발',
    item: ['베이비 (160mm 이하)', '리틀키즈 (165-220mm)', '주니어 (225-250mm)', '덩크 & 에어포스 1', '이지온', '샌들 & 슬리퍼', '라이프스타일', '축구','러닝','농구','조던']
  },
];

const sublist3 = [
  {
    id: 4,
    title: '의류',
    item: ['베이비 (0-3세)','리틀키즈 (4-7세)','주니어 (7-15세)','ACG','상하의 세트','탑 & 티셔츠','쇼츠','재킷 & 베스트','후디 & 크루','팬츠 & 레깅스','스커트 & 드레스','브라','수영복']
  }
];

const sublist4 = [
  {
    id: 5,
    title: '용품',
    item: ['모자','가방','양말','축구','농구']
  }
];

const renderSubList = (list) => (
  <Ul key={list.id}>
    {list.map((sub) => (
      <Li key={sub.id}>
        <A href="#">{sub.title}</A>
        {sub.item && sub.item.map((item, index) => <Ul  key={index}><Li><P>{item}</P></Li></Ul>)}
      </Li>
    ))}
  </Ul>
);

function Submenu2() {
  return (
    <Bg>
    <Wrapper>
      <Container>
        {renderSubList(sublist)}
        {renderSubList(sublist2)}
        {renderSubList(sublist3)}
        {renderSubList(sublist4)}
      </Container>
    </Wrapper>
    </Bg>
  );
}

export default Submenu2;
